@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
:root {
  font-size: 16px;
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Noto Sans', sans-serif;
  margin: 0;
  color: #feffff;
  background: #eeeeee;
}


.App {
	height: 100vh;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
}

footer {
	position: absolute;
	bottom: 5px;
	color: #0054c4;
	font-size: 1.25rem;
}

footer a {
	-webkit-text-decoration: None;
	        text-decoration: None;
	color: #0054c4;
	font-weight: 700;
	font-style: italic;
}

footer a:hover {
	text-decoration: underline;
	color: inherit;
}

.stock {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}


header {
	font-size: 1.25rem;
	font-weight: 700;
}

.stock-info{
	border-radius: 10px;
	background-color: #2f4052;
	box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 275px;
	padding: 35px;
}

@media (max-width: 570px) {
	.stock-info {
		height: 65%;
		padding: 7%;
	}
}

.price-info {
	text-align: left;
}

.company-name {
	font-size: 2rem;
	color: #71c2cc;
	font-weight: 700;
}

.current-price {
	font-size: 2.5rem;
	font-weight: 700;
}

.price-change {
	font-size: 1.25rem;
	font-weight: 700;
}

.stock-info .up {
	color: #aee931;
}

.stock-info .down {
	color: #d75650;
}

button {
	font: inherit;
	cursor: pointer;
	background-color: #0054c4;
	border: 1px solid #0054c4;
	color: #fefeff;
	padding: 0.75rem 1.75rem;
	border-radius: 6px;
	font-size: 1.5rem;
	box-shadow: 0 6px 20px rgba(0,118,255,0.23);
}

button:hover,
button:active {
	background-color: #1f64c1;
	border-color: #1f64c1;
}

.stock-card {
	background: #1d2937;
	height: 500px;
	width: 450px;
	border-radius: 20px;
	padding: 1.75rem;
	display: flex;
	align-items: center;
	justify-content: center;
	box-shadow: 0 1px 8px rgba(0, 0, 0, 0.4);
}

@media (max-width: 550px) {
	:root {
		font-size: 14px;
	}
	.stock-card {
		height: 50vh;
		width: 90%;
	}
}

@media (max-width: 370px) {
	:root {
		font-size: 12px;
	}
}

